.main-body {
  background-color: #0000;
  flex-direction: column;
  gap: 20px;
  padding: 50px 20px 0;
  display: flex;
}

.section-main-header {
  color: #fff;
  font-size: 20px;
}

.search-container {
  background-color: var(--main-gray);
  width: 1fr;
  border-radius: 5px;
  align-items: center;
  gap: 7px;
  padding: 8px;
  display: flex;
}

#search-field {
  width: 100%;
  height: 100%;
  color: #fff;
  border-color: #0000;
  background-color: #0000;
  border-inline: none;
}

#search-field:focus {
  outline: none;
}

.search-icon {
  color: #fff;
  cursor: pointer;
  transform: scale(.6);
}

.result-section {
  width: 100%;
  height: 470px;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  background-color: #ffffff03;
  border-radius: 5px;
  justify-content: center;
  display: flex;
}

.no-result {
  color: #ffffff1a;
  align-self: center;
  transform: scale(4);
}

