.main-body{
    background-color: transparent;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 0px 20px;
    padding-top: 50px;
}

.section-main-header{
    font-size: 20px;
    color: white;
}

.search-container{
    display: flex;
    background-color: var(--main-gray);
    align-items: center;
    gap: 7px;
    width: 1fr;
    padding: 8px;
    border-radius: 5px;

}

#search-field{
    width: 100%;
    height: 100%;
    background-color: transparent;
    border-color: transparent;
    color: white;
    border-inline: none;
}

#search-field:focus{
    outline: none;
}

.search-icon{
    color: white;
    transform: scale(0.6);
    cursor: pointer;
}

.result-section{
    width: 100%;
    height: 470px;
    background-color: rgba(255, 255, 255, 0.01);
    backdrop-filter: blur(5px);
    border-radius: 5px;
    display: flex;
    justify-content: center;
}

.no-result{
    align-self: center;
    transform: scale(4);
    color: rgba(255, 255, 255, 0.1);

}
